<template>
  <AppLayout>
    <ViewWrapper
      title="forms"
      :tab="tab"
      :tabs="_tabs"
      :breadcrumbs="breadcrumbs"
      @quickHelp="showQuickHelp"
    >
      <!-- header -->

      <template #header-actions>
        <BaseButton
          v-if="showAddForms"
          icon="eva-plus"
          label="add form"
          class="q-ml-md"
          @click="addForm"
        />
      </template>

      <!-- ... -->

      <!-- quick help -->

      <QuickHelp
        v-model="quickHelp"
        :page-help="'FORM'"
        @videoclick="videoHelp = true"
      />

      <!-- ... -->

      <!-- ... -->
      <QuickHelpModal
        v-model="videoHelp"
        :is-header="false"
        width="70vw"
        height="max-content"
        has-footer
      >
        <!-- Content -->

        <template #default>
          <div
            style="
              position: relative;
              padding-bottom: calc(50.70717423133236% + 42px);
              height: 0;
            "
            class="expand"
          >
            <iframe
              src="https://app.supademo.com/embed/clxd8vlxd1by1t2oe6icxf5nr"
              title="Workflow - Process Design"
              allow="clipboard-write"
              frameborder="0"
              webkitallowfullscreen="true"
              mozallowfullscreen="true"
              allowfullscreen
              style="
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
              "
            ></iframe>
          </div>
        </template>

        <template #footer>
          <BaseButton
            label="Close"
            icon="mdi-close"
            color="primary"
            @click="videoHelp = false"
          />
        </template>
        <!-- ...-->
      </QuickHelpModal>
      <!-- ...-->

      <!-- child routes -->

      <router-view :key="$route.fullPath" />

      <!-- ... -->
    </ViewWrapper>
  </AppLayout>
</template>

<script>
import AppLayout from "@/layouts/app/AppLayout.vue";
import ViewWrapper from "@/components/common/view-wrapper/ViewWrapper.vue";
import QuickHelp from "@/components/common/QuickHelp.vue";
import QuickHelpModal from "@/components/common/popup/QuickHelpModal.vue";

export default {
  name: "Forms",

  components: { AppLayout, ViewWrapper, QuickHelp, QuickHelpModal },

  data() {
    return {
      breadcrumbs: [
        {
          id: this.$nano.id(),
          label: "dashboard",
          route: "dashboard",
        },
        {
          id: this.$nano.id(),
          label: "forms",
          route: "forms-overview",
        },
        {
          id: this.$nano.id(),
          label: "",
          route: "",
        },
      ],
      tabs: [
        {
          id: this.$nano.id(),
          label: "overview",
          icon: "mdi-view-dashboard-outline",
          route: "forms-overview",
          access: false,
          accessLabel: "Overview",
        },
        {
          id: this.$nano.id(),
          label: "browse",
          icon: "eva-folder-outline",
          route: "forms-browse",
          access: false,
          accessLabel: "Manage Forms",
        },
        {
          id: this.$nano.id(),
          label: "trash",
          icon: "eva-trash-2-outline",
          route: "forms-trash",
          access: false,
          accessLabel: "Manage Forms",
        },
      ],
      tab: "forms-overview",
      quickHelp: false,
      videoHelp: false,
    };
  },

  computed: {
    showAddForms() {
      if (this.$store.state.profileMenus.length) {
        let menuList = this.$store.state.profileMenus.find(
          (row) => row.forms
        ).Menu;
        if (
          menuList["Manage Workflow Forms"] ||
          menuList["Manage Master Forms"] ||
          menuList["Manage SLA Forms"] ||
          menuList["Manage Task Forms"] ||
          menuList["Manage Feedback Forms"]
        ) {
          return true;
        }
      }

      return false;
    },

    _tabs() {
      if (this.$store.state.profileMenus.length) {
        if (
          this.$store.state.profileMenus.find((row) => row.forms).Menu[
            "Overview"
          ]
        ) {
          this.tabs.forEach((tab) => {
            if (tab.accessLabel === "Overview") {
              tab.access = true;
            }
          });
        }
        let menuList = this.$store.state.profileMenus.find(
          (row) => row.forms
        ).Menu;
        if (
          menuList["Manage Workflow Forms"] ||
          menuList["Manage Master Forms"] ||
          menuList["Manage SLA Forms"] ||
          menuList["Manage Task Forms"] ||
          menuList["Manage Feedback Forms"]
        ) {
          this.tabs.forEach((tab) => {
            if (tab.accessLabel === "Manage Forms") {
              tab.access = true;
            }
          });
        }
      }
      return this.tabs.filter((tab) => tab.access);
    },
  },

  watch: {
    $route: {
      immediate: true,
      deep: true,
      handler() {
        this.tab = this.$route.name;
        const index = this.breadcrumbs.length - 1;
        this.breadcrumbs[index].label = this.$route.meta.breadcrumb;
      },
    },
  },

  methods: {
    addForm() {
      this.$router.push({ name: "form-builder" });
    },

    showQuickHelp() {
      this.quickHelp = true;
    },
  },
};
</script>

<style lang="scss" scoped></style>
